import Vue from "vue";
import Vuex from "vuex";
import VuexPersistence from "vuex-persist";

Vue.use(Vuex);

const persistence = new VuexPersistence({
  storage: window.localStorage,
  reducer: state => ({ user: state.user })
});

export default new Vuex.Store({
  state: {
    user: {
      uuid: 0,
      name: "name",
      code: undefined
    },
    isAdmin: false,
    showNameForm: false,
    socket: {
      isConnected: false,
      identityNegotiated: false,
      message: "",
      reconnectError: false
    },
    game: {
      players: [],
      board: [],
      uncovered: [],
      winner: undefined,
      tileCount: { red: 0, blue: 0 }
    }
  },
  mutations: {
    SOCKET_ONOPEN(state, event) {
      Vue.prototype.$socket = event.currentTarget;
      state.socket.isConnected = true;
      state.socket.identityNegotiated = false;

      Vue.prototype.$socket.sendObj({
        action: "proposeUser",
        data: state.user
      });
    },
    SOCKET_ONCLOSE(state) {
      state.socket.isConnected = false;
    },
    SOCKET_ONERROR() {},
    // default handler called for all methods
    SOCKET_ONMESSAGE(state, message) {
      state.socket.message = message;
    },
    // mutations for reconnect methods
    SOCKET_RECONNECT() {},
    SOCKET_RECONNECT_ERROR(state) {
      state.socket.reconnectError = true;
    },
    setUser(state, user) {
      state.user = user;
      state.socket.identityNegotiated = true;
    },
    setAdmin(state, isAdmin) {
      state.isAdmin = isAdmin;
    },
    setGame(state, game) {
      state.game = game;
    },
    setNameForm(state, value) {
      state.showNameForm = value;
    }
  },
  actions: {
    SOCKET_setUser({ commit }, message) {
      commit("setUser", message.payload.user);
      commit("setAdmin", message.payload.isAdmin);
    },
    SOCKET_setGame({ commit }, message) {
      commit("setGame", message.payload);
    },
    updateName(_context, name) {
      Vue.prototype.$socket.sendObj({
        action: "setName",
        data: name
      });
    },
    makeMove(_context, index) {
      Vue.prototype.$socket.sendObj({
        action: "makeMove",
        data: index
      });
    },
    newGame() {
      Vue.prototype.$socket.sendObj({
        action: "newGame"
      });
    },
    skipTurn() {
      Vue.prototype.$socket.sendObj({
        action: "skipTurn"
      });
    },
    setCode(_context, code) {
      Vue.prototype.$socket.sendObj({
        action: "setCode",
        data: code
      });
    },
    toggleSpyMaster(_context, uuid) {
      Vue.prototype.$socket.sendObj({
        action: "toggleSpyMaster",
        data: uuid
      });
    }
  },
  plugins: [persistence.plugin]
});
