<template>
  <div>
    <div class="flexcontainer">
      <div
        v-bind:class="{
          current: turn == 'blue',
          blue: true,
          team: true,
          winner: winner == 'blue'
        }"
      >
        Blauw: {{ tilesLeft.blue }}
      </div>

      <div
        v-bind:class="{
          current: turn == 'red',
          red: true,
          team: true,
          winner: winner == 'red'
        }"
      >
        Rood: {{ tilesLeft.red }}
      </div>
      <button @click="skipTurn">Pas 👋</button>

      <button @click="newGame" v-if="isAdmin || winner">Nieuw spel 🤡</button>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "App",
  computed: mapState({
    turn: state => state.game.turn,
    tilesLeft: state => state.game.tileCount,
    winner: state => state.game.winner,
    isAdmin: state => state.isAdmin
  }),
  methods: {
    skipTurn() {
      this.$store.dispatch("skipTurn");
    },
    newGame: function() {
      this.$store.dispatch("newGame");
    }
  }
};
</script>

<style>
.team {
  padding: 1rem 0;
  width: 20rem;
}
.blue {
  color: rgb(53, 108, 181);
  border: 1px solid rgb(53, 108, 181);
}
.red {
  color: rgb(204, 0, 0);
  border: 1px solid rgb(204, 0, 0);
}
.current {
  border-width: 2px;
}
.current.red {
  background-color: rgba(204, 0, 0, 0.2);
}
.current.blue {
  background-color: rgba(53, 108, 181, 0.2);
}
.flexcontainer {
  display: flex;
}
.flexcontainer > * {
  margin: 1rem;
}
.winner {
  background-image: url("/balloons.jpg");
  background-size: cover;
}
</style>
