<template>
  <div :class="['board', winner ? '' : 'board--active']">
    <button
      v-for="(tile, index) in tiles"
      v-bind:key="index"
      :class="[
        'tile',
        tile.color ? `tile--${tile.color}` : '',
        tile.uncovered ? `` : 'tile--covered'
      ]"
      @click="click(index)"
    >
      {{ tile.word }}
    </button>
  </div>
</template>

<script>
export default {
  name: "board",
  computed: {
    tiles() {
      return this.$store.state.game.tiles;
    },
    uncovered() {
      return this.$store.state.game.uncovered;
    },
    winner() {
      return this.$store.state.game.winner;
    }
  },
  methods: {
    click(index) {
      if (this.winner) return;
      this.$store.dispatch("makeMove", index);
    }
  }
};
</script>

