<template>
  <div id="container" v-if="showName">
    <div id="form">
      <h2>He! Wat is je naam?</h2>
      <div class="field">
        <label class="label">Naam</label>
        <div class="control">
          <input v-model="name" />
        </div>
      </div>
      <!-- <div class="field">
        <label class="label">Room code</label>
        <div class="control">
          <input v-model="code" />
        </div>
      </div> -->
      <button @click="save">Go!</button>
      <button @click="cancel" v-if="showNameForm">No!</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data: function() {
    return {
      name: this.$store.state.user.name,
      code: this.$store.state.user.code
    };
  },
  computed: {
    showName() {
      return !this.$store.state.user.name || this.$store.state.showNameForm;
    },
    showNameForm() {
      return this.$store.state.showNameForm;
    }
  },
  methods: {
    save() {
      this.$store.dispatch("updateName", this.name);
      this.$store.dispatch("setCode", this.code);
      this.$store.commit("setNameForm", false);
    },
    cancel() {
      this.$store.commit("setNameForm", false);
    }
  }
};
</script>

<style scoped>
#container {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: white;
  display: flex;
}
#form {
  margin: auto;
}
</style>
