<template>
  <div>
    <ul>
      <li v-for="player in players" v-bind:key="player.user.uuid">
        {{ player.user.name }}
        (<span v-if="player.isAdmin">💂</span>
        <span
          v-if="player.isSpyMaster"
          @click="toggleSpyMaster(player.user.uuid)"
          >🕵️‍♀️</span
        >
        <span
          v-if="!player.isSpyMaster"
          @click="toggleSpyMaster(player.user.uuid)"
          >🤡</span
        >
        <span v-if="player.user.uuid == uuid" @click="toggleNameForm">✏️</span>
        )
      </li>
    </ul>
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "App",
  computed: mapState({
    players: state => state.game.players,
    uuid: state => state.user.uuid
  }),
  methods: {
    toggleSpyMaster: function(uuid) {
      this.$store.dispatch("toggleSpyMaster", uuid);
    },
    toggleNameForm() {
      this.$store.commit("setNameForm", true);
    }
  }
};
</script>

<style scoped>
ul {
  display: flex;
  flex-direction: column;
}
li {
  margin-left: 0;
  list-style: none;
}
.spymaster {
  opacity: 1;
}
</style>
